<template>
  <div>
    <header>
      <navbar>
      </navbar>
    </header>
    <section class="main" v-if="indexList">
      <p class="main-nav">
        <router-link to="/">
          总览
        </router-link>
        <span>> >{{indexList.name}}</span>
      </p>
      <section class="main-wrapper">
        <div class="brief">
          <p>{{indexList.image}}</p>
          <span><i>标签数：</i>1个 </span>
          <span><i>网页更新时间：</i>{{time|timeTrans}}</span>
        </div>
        <div class="sub-nav">
          <el-menu :default-active="activeIndex" mode="horizontal">
            <el-menu-item index="index">
              <router-link :to="{ path: 'index', query: data}">
                概述
              </router-link>
            </el-menu-item>
            <el-menu-item index="strategy">
              <router-link
                :to="{ path: 'strategy', query:data}">
                策略
              </router-link>
            </el-menu-item>
            <el-menu-item index="content">
              <router-link
                :to="{ path: 'content', query: data}">
                内容
              </router-link>
            </el-menu-item>
            <el-menu-item index="security">
              <router-link :to="{ path: 'security',
              query: data}">
                安全
              </router-link>
            </el-menu-item>
          </el-menu>
        </div>
        <transition name="fade" mode="out-in">
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </section>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
  import Navbar from 'components/navbar'
  import * as scanerApi from '@/api/transactionScaner2'

  export default {
    name: 'index',
    components: {
      Navbar
    },
    data() {
      return {
        indexList: '',
        time: '',
        activeIndex: '',
        data: {}
      }
    },
    methods: {},
    created() {
      // 拉取index页面的数据
      let data = this.$route.query
      this.data = data
      scanerApi.fetchImageDetail(data).then(res => {
        console.log(res.data.data)
        this.indexList = res.data.data
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .main
    width 88%
    margin: 0 auto
    position: relative
    top: 58px
    .main-nav
      font-size 1.6em
      color: #525252
      padding: 50px 0 25px 15px
      border-bottom: #E5E5E5 solid 1.2px
      a
        color: #4470E9
    .brief
      margin: 0 auto
      padding: 0 0 25px 0
      p
        font-size 1.6em
        color: #333333
        font-weight: 600
        margin: 45px 0 20px 0
      span
        font-size: 14px
        color: #4D4D4D
        margin: 0 35px 0px 0
        i
          color: #808080
          padding: 0px 2px 5px 0
      button
        line-height: 30px
        padding: 0 28px
        margin: -12px 0 0 10px
        border: #3E7AF6 solid 1px
        float right
        top: 10px
      i
        display: inline-block
        margin: -3px 2px 0 0
      .subscribe
        color: #FFFFFF
        background: #3E7AF6
      .collection
        color: #3E7AF6
        background: #FFFFFF

  section
    margin-left 60px
    margin-right 60px

  .icon-heart
    color: #3E7AF6
    font-size: 1.05em
    font-weight: 500

  .icon-subscriber
    color: #FFFFFF
</style>



